var _self;
export default {
	data() {
		return {
			info: {
				title: '',
				content: '',
			},
			id:0
		}
	},
	onLoad(e) {
		_self = this;
		_self.id = _self.$Route.query.id;
		this.getData();
	},
	methods: {
		getData() {
			_self.$ajax({
				path: _self.$api.article,
				data: {id:_self.id},
				filter() {
					return false
				},
				success(res) {
					let {
						data,
						code,
						msg
					} = res.data;
					if (code == 1) {
						_self.info = data
					}
				}
			})
		}
	}
}
